@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  color: white;
}

a {
    text-decoration: none;
    color: white;
}



header {
    height: 10vh;
    width: 100%;
    display: flex;
    align-items: center;
    background-color: #334257;
    color: white;
}

nav {
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.adjuster {
    flex: 1 1;
}

.title {
    flex: 1 1;
}

.title h3 {
    text-align: center;
}

.algorithms {
    display: flex;
    justify-content: space-around;
    flex: 1 1;
}

.selected {
    color: #DA0037;
}
.array {
    height: 60vh;
    max-width: 65%;
    margin: 0 auto;
    display: flex;
    
}
.bar {
    background-color: #5089C6;
    text-align: center;
    color: white;
    flex: 1 1;
}

.placeholder {
    font-size: 16px;
}
.button {
    transition: all .4s;
    padding: 1rem 4rem;
    background-color: #334257;
    color: white;
    position: absolute;
    top: 80%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%)
}

.button:hover {
    background-color: #DA0037;
}
